*::-webkit-scrollbar {
    width: 6px;               /* width of the entire scrollbar */
  }
  
  
  
  *::-webkit-scrollbar-thumb {
    background-color: #d8d8d8;    /* color of the scroll thumb */
    border-radius: 22px;       /* roundness of the scroll thumb */    
  }

body{
    margin:0;
    padding:0;
    box-sizing: border-box;
}